.wrapper {
  max-width: 600px;
  display: flex;
  flex-direction: column;
}

.container {
  overflow-y: scroll;
  flex-grow: 1;
}

.order_sum {
  display: flex;
  flex-direction: row;
  margin-right: 70px;
  margin-top: 40px;
  align-items: center;
  justify-content: flex-end;
}

.order_container {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 40px;
}

.element_item {
  display: flex;
  flex-direction: row;
  margin-right: 10px;
  align-items: center;
}

.empty {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.loading {
  pointer-events: none;
  opacity: 0.7;
}
